body {
  /* font-family: 'Montserrat', sans-serif; */
  font-family: 'Roboto', sans-serif;
  color: black;
  font-weight: 100;
  scroll-snap-type: y mandatory;
  overflow-y: scroll;
}

h1 {
  font-family: 'Poppins', serif;
  /* font-family: 'Yeseva One', cursive; */
  /* font-family: 'Lora', serif; */
  /* font-style: italic; */
  font-weight: 700;
  color: mediumvioletred;
  /* color: crimson; */
  display: block;
}

.title {
  margin: 0;
  text-align: right;
  font-size:35px;
}
p {
  font-size: 15px;
  line-height: 23px;
  color: black;
}

a {
  color: black;
  background-color: transparent;
}
a:hover {
  color: mediumvioletred;
  /* color: crimson; */
}

.nav-link:hover {
  text-decoration: underline;
}
.page {
  height: 100vh;
  display: flex;
  align-items: center;
  scroll-snap-align: center;
}

.content-page {
  flex-direction: column;
  justify-content: center;
}

.about-me-container {
  display: flex;
  align-content: center;
  justify-content: flex-start;
  flex-wrap: wrap;
}

.about-me-paragraph {
  width: 300px;
  display: flex;
  align-content: center;
  justify-content: center;
  flex-wrap: wrap;
  text-align: right;
}

.header-bg {
  background-color:white;
  border-radius: 5px;
  padding: 0 15px;
}

.navbar-links {
  padding-top: 0;
  padding-bottom: 3px;
}

.hide-nav {
  -webkit-transition: 1s;
  -moz-transition: 1s;
  -o-transition: 1s;
  transition: 1s;
  opacity: 0;
  cursor: pointer;
}

.show-nav {
  -webkit-transition: 1s;
  -moz-transition: 1s;
  -o-transition: 1s;
  transition: 1s;
  opacity: 1;
  cursor: pointer;
}

.resume-container {
  display: flex;
  flex-direction: column;
}

.resume-item {
  display: flex;
  justify-content: flex-end;
}

.resume-text {
  text-align: right;
  max-width: 30vw;
}

.resume-divider {
  width: 1px;
  background-color: black;
  margin: 0 20px;
}

.text-right {
  text-align: right;
}

