.expand-resume-box {
  -webkit-transition: 1s;
  -moz-transition: 1s;
  -o-transition: 1s;
  transition: 1s;
  opacity: 1;
  width: 400px;
  height: 100%;
  border-radius: 5px;
  padding: 5px;
  background-color: #002447;
  margin: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  cursor: pointer;
}

.contract-resume-box {
  -webkit-transition: 1s;
  -moz-transition: 1s;
  -o-transition: 1s;
  transition: 1s;
  opacity: 1;
  width: 400px;
  height: 60px;
  border-radius: 5px;
  padding: 5px;
  background-color: white;
  margin: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  cursor: pointer;
}

.resume-box {
  width: 350px;
  height: 60px;
  border-radius: 5px;
  background-color: white;
  margin: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.border-right {
  border-right: 1px solid black !important;
}

.border-left {
  border-left: 1px solid black !important;
}

.padding-right {
  padding-right: 20px;
}

.padding-left {
  padding-left: 20px;
}

