.flex-center-right {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: center;
}

.about-flex-collapse {
  display: flex;
}
@media (max-width: 960px) {
  .about-flex-collapse {
    flex-direction: column-reverse;
    align-items: center;
  }
}

.flex-end {
  display: flex;
  justify-content: flex-end;
}

.flex-start {
  display: flex;
  justify-content: flex-start;
}